.header{
    display : flex;
    justify-content: space-between;
    align-items : center;
    padding : 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index:10;
    transition: 0.3s;
}

.header-bg{
    background-color: rgba(0,0,0,8.5);
    transition: 0.5s;
}

.nav-menu{
    display : flex;
}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu a {
    font-size : 1.2rem;
    font-weight : 500;
}

.hamburger{
    display: none;
}

/*TO MAKE THE PAGE RESPOND TO SMALLER SCREENS OF MOBILES */
@media screen and (max-width: 1040px ){
    .nav-menu{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width:100%;
        height:100vh;
        background:rgba(0,0,0,0.9);
        /*  TO KEEP THE LEFT HEADING INTACT */
        position:absolute;
        top:0;
        left:-100%;
        z-index:-4;
        transition :0.3s ;
    }

    .nav-menu.active{
        left : 0 ;
        
    }
    .nav-menu li {
        padding: 1rem 0 ;
    }
    
    .nav-menu a {
        font-size : 2rem;
        font-weight : 500;
    }

    .hamburger{
        display: initial;   /*only displayed on a shorter screen*/
    }
}
